import React, { useEffect } from 'react';
import Popping from 'poppingjs'
import Dragger from 'draggerjs'
import { PoppingFlip } from 'poppingjs/modifiers'
import { PoppingResize } from 'poppingjs/plugins'
import { SectionWithCode } from 'Templates';
import { SNAPSHOT } from 'Common/Utils';
import * as code from '../StringCodes';

export const Fliping = () => { 
    useEffect(() => {
        if(SNAPSHOT) return;
        const PoppingDrag = (instance) => {
            const dragger = new Dragger('#pop-flipping', {
                draggable: 'img'
            })

            dragger.on('dragstart', () => instance.target.classList.remove('popping-center'))
            dragger.on('dragmove', () => popping.updatePosition())
            return {
                destroy() {
                    dragger.destroy()
                }
            }
        }
        const target = document.querySelector('#pop-flipping img')
        const dialog = document.querySelector('#pop-flipping .popping')
        const popping = Popping.create(target, dialog, { 
            modifiers: [PoppingFlip],
            allowFlipManifestation: true,
            plugins: [PoppingResize, PoppingDrag]
        })
        
        return () => popping.destroy()
    }, [])

    return (
        <>  
            <SectionWithCode
                readonly
                title='Flipping'
                context={<>to enable flipping you must apply <b>PoppingFlip</b> to the <b>modifiers</b></>}
                code={code.Fliping}
            >
                <div className="popping-container n-flat" id="pop-flipping" data-example>  
                    <img 
                        className="popping-center"
                        src="/images/icons/icon-96x96.png" 
                        alt="beard-guy"
                        width="96"
                        height="96"
                        draggable={false}/>
                    <div className="popping n-bg-intense">
                        Drag me to edges 
                    </div> 
                </div>
            </SectionWithCode>
        </>
    );
}