export const Importing = `import Popping from "poppingjs"
import { PoppingFlip } from "poppingjs/modifiers"
import { PoppingScroll, PoppingResize } from "poppingjs/plugins"`

const Common = `.popping-container {
    position: relative;
    display: block;
    height: 300px;
    border-radius: 15px;
}
.popping {
    position: absolute;
    left: 0;
    top: 0;
    max-width: 300px; 
    padding: 15px 20px;
    border-radius: 2px;
    transition: opacity 150ms ease-in-out;
    z-index: 1029;
    box-shadow: 1px 3px 11px rgba(0, 0, 0, 0.3);
}
.popping[data-popping-placement^='top'] > [data-popping-arrow='true'] {
    bottom: -5px;
    box-shadow: 4px 2px 5px rgba(0, 0, 0, 0.05);
}
.popping[data-popping-placement^='bottom'] > [data-popping-arrow='true'] {
    top: -5px;
    box-shadow: -4px -2px 5px rgba(0, 0, 0, 0.05);
}
.popping[data-popping-placement^='right'] > [data-popping-arrow='true'] {
    left: -5px;
    box-shadow: -4px 2px 5px rgba(0, 0, 0, 0.05);
}
.popping[data-popping-placement^='left'] > [data-popping-arrow='true'] {
    right: -5px;
    box-shadow: 4px -2px 5px rgba(0, 0, 0, 0.05);
}
.popping > [data-popping-arrow='true'] {
    display: block;
    width: 12px;
    height: 12px;
    position: absolute;
    z-index: -1;
    background-color: inherit;
}`
const CommongWidthCenter = `${Common}
.popping-center {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -48px;
    margin-left: -48px;
}`
export const GettingStarted = {
html: `<div class="popping-container" id="getting-started">
    <img 
        class="popping-center" 
        src="/images/icons/icon-96x96.png"
        alt="beard-guy"
        width="96" 
        height="96"/>
    <div class="popping n-bg">
        mytabworks 
    </div>
</div>`,
css: CommongWidthCenter,
js: `const target = document.querySelector('#getting-started > img')
const dialog = document.querySelector('#getting-started > .popping')
Popping.create(target, dialog, {
    placement: 'top',
})`
}

export const CreatePlugin = {
html: `<div class="popping-container n-flat" id="pop-create-plugin">  
    <img 
        class="popping-center"
        src="/images/icons/icon-96x96.png" 
        alt="beard-guy"
        width="96"
        height="96"
        draggable={false}/>
    <div class="popping n-bg-intense">
        Drag me
    </div> 
</div>`,
css: CommongWidthCenter,
js: `const PoppingDrag = (instance) => {
    const dragger = new Dragger('#pop-create-plugin', {
        draggable: 'img'
    })

    dragger.on('dragstart', () => instance.target.classList.remove('popping-center'))
    dragger.on('dragmove', () => popping.updatePosition())
    return {
        destroy() {
            dragger.destroy()
        }
    }
}
const target = document.querySelector('#pop-create-plugin img')
const dialog = document.querySelector('#pop-create-plugin .popping')
const popping = Popping.create(target, dialog, {
    plugins: [PoppingDrag]
})`
}

export const Fliping = {
html: `<div class="popping-container n-flat" id="pop-flipping">  
    <img 
        class="popping-center"
        src="/images/icons/icon-96x96.png" 
        alt="beard-guy"
        width="96"
        height="96"
        draggable={false}/>
    <div class="popping n-bg-intense">
        Drag me to edges 
    </div> 
</div>`,
css: CommongWidthCenter,
js: `const PoppingDrag = (instance) => {
    const dragger = new Dragger('#pop-flipping', {
        draggable: 'img'
    })

    dragger.on('dragstart', () => instance.target.classList.remove('popping-center'))
    dragger.on('dragmove', () => popping.updatePosition())
    return {
        destroy() {
            dragger.destroy()
        }
    }
}
const target = document.querySelector('#pop-flipping img')
const dialog = document.querySelector('#pop-flipping .popping')
const popping = Popping.create(target, dialog, { 
    modifiers: [PoppingFlip], 
    plugins: [PoppingDrag],
    allowFlipManifestation: true,
})`
}

export const Persistent = {
html: `<div class="popping-container popping-scroll n-flat" id="pop-persistent"> 
    <div style="height: 1000px; width: 1000px"/>
    <div class="text-center">
        <img width="96" height="96" src="/images/icons/icon-96x96.png" alt="beard-guy"/>
    </div>
    <div class="popping n-bg-intense">
        Scroll me 
    </div>
    <div style="height: 1000px"/>
</div>`,
css: `${Common}
.popping-scroll {
    overflow: scroll;
}
.text-center {
    text-align: center;
}`,
js: `const container = document.querySelector('#pop-persistent')
container.scrollTop = container.scrollHeight/2 - container.clientHeight/2
const target = document.querySelector('#pop-persistent img')
const dialog = document.querySelector('#pop-persistent .popping')
const popping = Popping.create(target, dialog, { 
    modifiers: [PoppingFlip],
    plugins: [PoppingScroll],
    allowPersistent: true
})`
}

export const Animated = {
html: `<div class="popping-container n-flat" id="pop-animated" data-example>
    <img 
        className="popping-center"
        src="/images/icons/icon-96x96.png" 
        alt="beard-guy"
        width="96" 
        height="96"/>
    <div class="popping popping-animated n-bg-intense">
        you can see me
    </div>
</div>`,
css: `${CommongWidthCenter}
.popping-container > img + .popping-animated {
    opacity: 0;
    transition: opacity 300ms ease-in-out;
}
.popping-container > img:hover + .popping-animated {
    opacity: 1;
}`,
js: `const target = document.querySelector('#pop-animated > img')
const dialog = document.querySelector('#pop-animated > .popping')
const popping = Popping.create(target, dialog, {
    placement: 'top'
})`
}