import React, { useEffect } from 'react';
import Popping from 'poppingjs'
import { PoppingFlip } from 'poppingjs/modifiers'
import { PoppingScroll, PoppingResize } from 'poppingjs/plugins'
import { SectionWithCode } from 'Templates';
import { SNAPSHOT } from 'Common/Utils';
import * as code from '../StringCodes'; 

export const Persistent = () => { 
    useEffect(() => {
        if(SNAPSHOT) return;
        const container = document.querySelector('#pop-persistent')
        container.scrollTop = container.scrollHeight/2 - container.clientHeight/2
        const target = document.querySelector('#pop-persistent img')
        const dialog = document.querySelector('#pop-persistent .popping')
        const popping = Popping.create(target, dialog, { 
            modifiers: [PoppingFlip],
            plugins: [PoppingScroll, PoppingResize],
            allowPersistent: true
        })

        return () => popping.destroy()
    }, [])

    return (
        <>  
            <SectionWithCode
                readonly
                title='Persistent'
                context={<>by applying <b>PoppingScroll</b>(to update the dialog on every scroll) into the <b>plugins</b> and setting the option <b>allowPersistent</b> into  <b>true</b></>}
                code={code.Persistent}
            >
                <div className="popping-container popping-scroll n-flat" id="pop-persistent" data-example> 
                    <div style={{height: '1000px', width: '2000px'}}/>
                    <div className="text-center">
                        <img width="96" height="96" src="/images/icons/icon-96x96.png" alt="beard-guy"/>
                    </div>
                    <div className="popping n-bg-intense">
                        Scroll me 
                    </div>
                    <div style={{height: '1000px'}}/>
                </div>
            </SectionWithCode>
        </>
    );
}