import React, { useEffect } from 'react';
import Popping from 'poppingjs';
import { PoppingResize } from 'poppingjs/plugins';
import { SectionWithCode } from 'Templates';
import { SNAPSHOT } from 'Common/Utils';
import * as code from '../StringCodes'; 

export const Animated = () => {
    useEffect(() => { 
        if(SNAPSHOT) return;
        const target = document.querySelector('#pop-animated > img')
        const dialog = document.querySelector('#pop-animated > .popping')
        const popping = Popping.create(target, dialog, {
            placement: 'top',
            plugins: [PoppingResize]
        })

        return () => popping.destroy()
    }, [])

    return (
        <>  
            <SectionWithCode
                readonly
                title='Animated'
                context='animating to popping when hovering or clicking.'
                code={code.Animated}
            >
                <div className="popping-container n-flat" id="pop-animated" data-example>
                    <img 
                        className="popping-center"
                        src="/images/icons/icon-96x96.png" 
                        alt="beard-guy"
                        width="96" 
                        height="96"/>
                    <div className="popping popping-animated n-bg-intense">
                         you can see me
                    </div>
                </div>
            </SectionWithCode>
        </>
    );
}