import React from 'react';
import { Section, Table } from 'Templates';

const PoppingRows = [
    {
		name: 'Popping.create(',
		type: '',
		default: '',
		description: '',
	},
	{
		name: 'target',
		type: 'element | node',
		default: '-',
		description: 'the element you want to place your dialog.',
	},
	{
		name: 'dialog',
		type: 'element | node',
		default: '-',
		description: 'it is the element of tooltip dialog suppose to be pops next to the target.',
	},
	{
		name: 'options',
		type: 'object',
		default: '-',
		description: 'the options of the popping',
		data: [
			{
				name: '{',
				type: '',
				default: '',
				description: '',
			},
			{
				name: 'modifiers',
				type: 'array',
				default: '-',
				description: <>these are the functions that mutates the poppings abilities into some extent. <b>PoppingFlip</b> is the only given modifier.</>,
			},
			{
				name: 'plugins',
				type: 'array',
				default: '-',
				description: <>these are the functions that update the dialog on some certain event. There are <b>2</b> given plugins:<br/>
				<b>PoppingScroll</b>(that update the position when scrolling) and <b>PoppingResize</b>(that update the position when resizing or orienting)</>,
			},
			{
				name: 'placement',
				type: `'top' | 'top-left' | 'top-right' | 
				'bottom' | 'bottom-left' | 'bottom-right' | 
				'left' | 'left-top' | 'left-bottom' | 
				'right' | 'right-top' | 'right-bottom'`,
				default: 'bottom',
				description: 'it is the placement or position of the tooltip dialog.',
			},
			{
				name: 'allowFlipManifestation',
				type: 'boolean',
				default: 'false',
				description: <>when set <b>true</b> and <b>PoppingFlip</b> is put in <b>modifiers</b> then every placement flip will be permanent.</>,
			},
			{
				name: 'allowPersistent',
				type: 'boolean',
				default: 'false',
				description: <>when set <b>true</b> and <b>PoppingScroll</b> is put in <b>plugins</b> then the tooltip dialog will be persistent to show.</>,
			},
			{
				name: 'rotateArrow',
				type: 'number',
				default: '45',
				description: 'it is the rotation of the arrow',
			},
			{
				name: 'useTranslatePositioning',
				type: 'boolean',
				default: 'false',
				description: <>when set <b>true</b> the positioning will be base on transform translate and not by top and left</>,
			},
			{
				name: '}',
				type: '',
				default: '',
				description: '',
			},
		]
	},
	{
		name: ')',
		type: '',
		default: '',
		description: '',
	},
]

const MethodsRows = [
	{
		name: 'Instance.updatePosition',
		type: 'void',
		default: '-',
		description: 'it is use to update the position of the tooltip dialog.',
	},
	{
		name: 'Instance.updateOptions',
		type: 'void',
		default: '-',
		description: 'it is use to update options.',
		data: [
			{
				name: 'Instance.updateOptions(',
				type: '',
				default: '',
				description: '',
			},
			{
				name: 'options',
				type: 'object',
				default: '-',
				description: 'the options you wanna update.',
			},
			{
				name: '): void',
				type: '',
				default: '',
				description: '',
			},
		]
	},
	{
		name: 'Instance.destroy',
		type: 'void',
		default: '-',
		description: 'it is use to destroy and kill the eventListeners.',
	},
]

export const Popping = () => (
	<Section
		title='Popping'>
		<Table.ForAPI nameLabel="Argument" data={PoppingRows} />
	</Section>
);

export const Methods = () => (
	<Section
		title='Popping Methods'>
		<Table.ForAPI nameLabel="Method" typeLabel="Return" data={MethodsRows} />
	</Section>
);