import React, { useEffect } from 'react';
import Popping from 'poppingjs'
import Dragger from 'draggerjs' 
import { PoppingResize } from 'poppingjs/plugins'
import { SectionWithCode } from 'Templates';
import { SNAPSHOT } from 'Common/Utils';
import * as code from '../StringCodes'; 

export const CreatePlugin = () => { 
    useEffect(() => { 
        if(SNAPSHOT) return;
        const PoppingDrag = (instance) => {
            const dragger = new Dragger('#pop-create-plugin', {
                draggable: 'img'
            })

            dragger.on('dragstart', (e) => {
                e.preventDefault()
                instance.target.classList.remove('popping-center')
            })
            dragger.on('dragmove', () => popping.updatePosition())
            return {
                destroy() {
                    dragger.destroy()
                }
            }
        }
        const target = document.querySelector('#pop-create-plugin img')
        const dialog = document.querySelector('#pop-create-plugin .popping')
        const popping = Popping.create(target, dialog, {
            plugins: [PoppingResize, PoppingDrag]
        })
        
        return () => popping.destroy()
    }, [])

    return (
        <>  
            <SectionWithCode
                readonly
                title='Create Plugin'
                context='you can create your own plugin.'
                code={code.CreatePlugin}
            >
                <div className="popping-container n-flat" id="pop-create-plugin" data-example>  
                    <img 
                        className="popping-center"
                        src="/images/icons/icon-96x96.png" 
                        alt="beard-guy"
                        width="96"
                        height="96"
                        draggable={false}/>
                    <div className="popping n-bg-intense">
                        Drag me
                    </div> 
                </div>
            </SectionWithCode>
        </>
    );
}