import React, { useEffect, useState } from 'react';
import Popping from 'poppingjs'
import { PoppingResize } from 'poppingjs/plugins'
import { SectionWithCode, Note } from 'Templates';
import { SNAPSHOT } from 'Common/Utils';
import * as code from '../StringCodes'; 

export const GettingStarted = () => {
    const [state, setState] = useState('top')
    useEffect(() => {
        if(SNAPSHOT) return;
        const target = document.querySelector('#pop-getting-started > img')
        const dialog = document.querySelector('#pop-getting-started > .popping')
        const popping = Popping.create(target, dialog, {
            placement: state,
            plugins: [PoppingResize]
        })

        return () => popping.destroy()
    }, [state])

    const placements = [
        'top', 'top-left', 'top-right', 
        'bottom', 'bottom-left', 'bottom-right',
        'left', 'left-top', 'left-bottom',
        'right', 'right-top', 'right-bottom'
    ]

    return (
        <>  
            <SectionWithCode
                readonly
                title='Getting Started'
                context='we are getting started using poppingjs.'
                code={code.GettingStarted}
            >
                <div className="popping-container n-flat" id="pop-getting-started" data-example>
                    <div className="popping-playground">
                        {placements.map((placement) => (
                            <div className={`radio-${placement}`} key={placement}>
                                <input 
                                    className="n-form-control" 
                                    type="radio" 
                                    name="placement" 
                                    value={placement} 
                                    title={placement}
                                    checked={state === placement} 
                                    onChange={(e) => setState(e.target.value)}
                                />
                            </div>
                        ))} 
                    </div>
                    <img 
                        className="popping-center" 
                        src="/images/icons/icon-96x96.png"
                        alt="beard-guy"
                        width="96" 
                        height="96"/>
                    <div className="popping n-bg-intense">
                        {state} 
                    </div>
                </div>
            </SectionWithCode>
            <Note
                variant='info'
                context={<>when one of the parent or ancestor element has <b>position: relative;</b> that will be the base of the tooltip positioning or else it will be the <b>html</b> element itself.</>}
                style={{ marginTop: '-20px', marginBottom: '20px' }}
            />
        </>
    );
}