import React from 'react'; 
import { MainContainer, SidePanel, Article, HeadGear } from 'Templates';
import * as Section from './Sections';
import * as Table from './Tables';

const Poppingjs = () => { 
	const title = 'Poppingjs (not publish yet)';
	const context = `is a tooltip & popover positioning engine which is delightfully made in pure vanila javascript without dependency what so ever.`;

	return (
		<>
			<HeadGear>
				<HeadGear.Meta 
					title={`${title} - Javascript Libraries | Mytabworks `}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
			</HeadGear>
			<MainContainer 
				title={title}
				context={context}
				main={<PoppingjsMain />}
				side={<PoppingjsSidePanel />}
			/>
		</>
	);
};

Poppingjs.propTypes = {}; 

const PoppingjsMain = () => (
	<>
		<Section.Installation />
		<Section.Importing />
		<Section.GettingStarted /> 
		<Section.CreatePlugin />
		<Section.Fliping /> 
		<Section.Persistent />
		<Section.Animated />
		<Article title='API'>
			<Table.Popping />
			<Table.Methods />
		</Article>
	</>
);

const PoppingjsSidePanel = () => {
	const list = [
		{ label: 'Installation' },
		{ label: 'Imports' },
		{
			label: 'Getting Started',
			sub: [
				{ label: 'Create Plugin' },
				{ label: 'Flipping' },
				{ label: 'Persistent' },
				{ label: 'Animated' },
			],
		},
		{
			label: 'API',
			sub: [
				{ label: 'Popping' },
				{ label: 'Popping Methods' },
			],
		},
	];
	return <SidePanel.ScrollNav list={list} />;
};

export default Poppingjs